
* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",Arial,sans-serif;
}
a {
  color: #545455;
  cursor: pointer;
  display: block;
}
.clear {
  clear: both;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: bold;
  line-height: 1.5;
}
h2,
h3,
h4,
h5,
h6 {
  margin: 20px 0 15px;
}
h1 {
  font-size: 22px;
}
@media (max-width: 767px) {
h1 {
    font-size: 18px;
}
}
h2 {
  font-size: 20px;
}
@media (max-width: 767px) {
h2 {
    font-size: 16px;
}
}
h3 {
  font-size: 18px;
}
@media (max-width: 767px) {
h3 {
    font-size: 14px;
}
}
h4 {
  font-size: 16px;
}
@media (max-width: 767px) {
h4 {
    font-size: 12px;
}
}
h5 {
  font-size: 14px;
}
@media (max-width: 767px) {
h5 {
    font-size: 10px;
}
}
h6 {
  font-size: 12px;
}
@media (max-width: 767px) {
h6 {
    font-size: 8px;
}
}
p {
  margin: 0 0 25px 0;
}
body {
  background-color: #f5f7f9;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}
.black_line {
  width: 100%;
  height: 2px;
  background: #000;
}
#main {
  overflow: hidden;
  width: 960px;
  text-align: center;
  margin: auto;
}
#content {
  width: 700px;
  float: left;
  background: white;
}

.article_wrap {
  padding: 40px;
}
.article_title {
  display: inline-block;
  color: #222;
  font-size: 34px;
  font-weight: 600;
  border-bottom: 1px solid white;
  cursor: pointer;
}
.article_title:hover {
  border-bottom: 1px solid #222;
}
.article_info {
  color: #999;
  font-size: 14px;
  padding-top: 8px;
}
.tag_margin {
  margin: 3px;
}
.article_gist {
  text-align: left;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 16px;
}
.article_button {
  display: inline-block;
  padding: 3px 12px;
  border: 2px solid #222;
  color: #222;
  font-size: 14px;
  cursor: pointer;
}
.article_all:hover {
  color: white;
  background: #000;
  font-weight: 600;
}
.article_underline {
  height: 1px;
  width: 40px;
  background: #545455;
  margin: 80px auto 0;
}

#left_menu {
  float: left;
  width: 240px;
  margin-right: 20px;
}
@media (max-width: 767px) {
#left_menu {
    display: none;
}
}
#left_menu a:hover {
  color: #222;
  background: #f5f7f9;
}
.menu_name {
  background: rgb(38, 42, 48);
  color: white;
  padding: 10px 0;
}
nav {
  background: white;
  padding: 20px 0;
}
.nav {
  padding: 8px 20px;
  text-align: left;
}
.info {
  margin-top: 10px;
  padding: 20px;
  background: white;
}
.info_name {
  font-weight: 600;
  padding: 5px;
}
.info img {
  width: 120px;
  height: 120px;
}
.archive {
  padding-top: 20px;
}
.archive .archive_count {
  display: block;
  color: #222;
  font-weight: 600;
  font-size: 16px;
}
.archive .archive_name {
  display: block;
  color: #999;
  font-size: 14px;
}
.communication {
  padding-top: 20px;
}
.communication_item {
  display: inline-block;
  width: 40%;
  font-size: 14px;
  font-weight: 600;
  padding: 5px;
}

.article_detail_title {
  cursor: default;
  margin: 40px 0 0;
}
.article_detail_content {
  text-align: left;
  padding: 60px 0;
  font-size: 18px;
}
/*

Atom One Light by Daniel Gamage
Original One Light Syntax theme from https://github.com/atom/one-light-syntax

base:    #fafafa
mono-1:  #383a42
mono-2:  #686b77
mono-3:  #a0a1a7
hue-1:   #0184bb
hue-2:   #4078f2
hue-3:   #a626a4
hue-4:   #50a14f
hue-5:   #e45649
hue-5-2: #c91243
hue-6:   #986801
hue-6-2: #c18401

*/

pre {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #383a42;
  background: #fafafa;
}

code {
  display: inline-block;
  overflow-x: auto;
  padding: 0.3em;
  color: #383a42;
  background: #fafafa;
}

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #383a42;
  background: #fafafa;
}

.hljs-comment,
.hljs-quote {
  color: #a0a1a7;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #a626a4;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e45649;
}

.hljs-literal {
  color: #0184bb;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #50a14f;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #c18401;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #986801;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #4078f2;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}
